@font-face {
  font-weight: 400;
  font-family: "PTMono";
  font-style: normal;
  src: url("../fonts/PTMono-Regular.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "RobotoCondensed";
  font-style: bold;
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  padding: 0;
  margin: 0;
  font-family: "PTMono", "Arial Narrow", sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: #ffffff;
  background-color: #1a2931;
  background-size: 320px auto;
  counter-reset: section; }

a {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0); }

.button {
  display: block;
  width: 287px;
  height: 55px;
  font-family: inherit;
  color: #ffffff;
  background-color: #00b2ff;
  text-transform: uppercase;
  border: 0;
  text-align: center;
  padding: 17px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }
  .button:focus, .button:hover {
    background-color: #1a2931; }
  .button:active {
    background-color: #1a2931;
    color: #ffffff; }

.container {
  margin: 0 auto;
  width: 100%; }

.container--index {
  position: relative;
  display: block;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
  padding-right: 0; }

@media (max-width: 320px) {
  .space {
    content: ""; }
    .space::after {
      content: "\00a0"; } }

@media (min-width: 768px) {
  .container {
    width: 100%; } }

@media (min-width: 1024px) {
  .container {
    width: 100%; } }

.main-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row; }

.main-nav__toggle {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  border: none; }

.page-header {
  max-width: 320px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding-top: 17px;
  padding-left: 17px;
  padding-right: 15px; }
